.navbar__optionLineOne {
    font-size: 14px;
    color: black;
    font-weight: bold;
    text-decoration: none;
}

.navbar__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.navbar__optionCart {
    display: flex;
    align-items: center;
    color: black;
}

.navbar__cartCount {
    margin-left: 10px;
    margin-right: 10px;
}

.navbar__nav {
    display: flex;
    justify-content: flex-end;
}

.navbar__option {
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}

.link {
    text-decoration: none;
}
.home {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__container {}

.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    width: 100%;
}
.product {
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    background-color: white;
    z-index: 1;
}

.product__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.product__rating {
    display: flex;
    color: rgb(150, 124, 255);
}

.product__content>img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__content>button {
    background: rgb(150, 124, 255);
    border: 1px solid rgb(150, 124, 255);
    margin-top: 10px;
    /* border-color: #a88734 #9c7e31 #846a29; */
    color: #111;
    /* width: 200px; */
    height: 25px;
    border-radius: 8px;
}

.product__price {
    margin-top: 5px;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__user {
    display: flex;
    flex-direction: row;
    padding: 5px;
}

.product__profileimage {
    position: relative;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 8px;

}
.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
}

.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
}

.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 30px;
    padding: 20px;
    background-color: #fff;
    border: 1px solid rgb(150, 124, 255);
    border-radius: 10px;
}

.subtotal__gift {
    display: flex;
    align-items: center;
}

.subtotal__gift>input {
    margin-right: 5px;
}
.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct__info {
    padding-left: 20px
}

.checkoutProduct__info>button {
    background: rgb(150, 124, 255);
    border: 1px solid;
    margin-top: 10px;
    border-color: #fff;
    color: #111;
    border-radius: 8px;
    height: 25px;
}

.checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct__rating {
    display: flex;
    color: rgb(150, 124, 255);
}

.checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
}
.register {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.register__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}


.register__container {
    width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;

}

.register__container>h1 {
    font-weight: 500;
    margin-bottom: 20px;
}


.register__container>form>input {
    height: 30px;
    width: 98%;
    margin-bottom: 10px;
    background-color: white;
}

.register__container>form>h5 {
    margin-bottom: 5px;
}

.register__container>p {
    font-size: 12px;
    margin-top: 15px;
}

.register__registerButton {
    text-align: center;
    width: 100%;
    height: 30px;
    margin-top: 20px;
    background-color: rgb(150, 124, 255);
    border-color: rgb(150, 124, 255);
    border-radius: 10px;
}

.register__error {
    font-size: 12px;
    color: red;
}
.login {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}

.login__container {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}

.login__container>h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container>form>h5 {
  margin-bottom: 5px;
}

.login__container>form>input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}

.login__container>p {
  margin-top: 15px;
  font-size: 12px;
}

.login__signInButton {
  background: rgb(150, 124, 255);
  width: 100%;
  height: 30px;
  border: 1px solid rgb(150, 124, 255);
  margin-top: 10px;
  border-radius: 8px;
  /* border-color: #a88734 #9c7e31 #846a29; */
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
}
